.pagination {
    width: 265px;
    margin: 15px auto;
    text-align: center;
}

.pagination-list-container {
    width: 175px;
    overflow: hidden;
}

.pagination-page-list {
    position: relative;
    margin: 0 auto;
    padding: 0;
    display: inline-flex;
}

.pagination-page-list .list-item {
    list-style: none;
    width: 35px;
}

.pagination-page-list .list-item:hover {
    cursor: pointer;
}

.pagination-page-list .list-item.selected{
    color: lightgray;

}

.pagination-btn {
    width: 45px;
}

.pagination-btn:hover {
    cursor: pointer;
}
