.header {
    justify-content: flex-start;
}

.header .navbar-brand {
    margin: 0 40px;
    color: #28a745;
}

.header .navbar-brand:hover {
    color: darkgreen;
}

.header .navbar-nav {
    flex-direction: row;
    margin: 0 20px;
}

.header .nav-item {
    margin-right: 20px;
}

.header .nav-link {
    color: white;
    text-decoration: none;
}

.header .nav-link:hover {
    color: lightgray;
}