.random-character {
    height: 270px;
    margin: 0;
    padding: 20px 20px;
    font-size: 18px;
}

.random-character .character-image{
    width: 225px;
    height: 225px;
    margin-right: 15px;
}

.random-character h4 {
    font-size: 26px;
}

.random-character p{
    margin: 0;
}

.random-character .section{
    margin-top: 0px;
}

.random-character .section span{
    font-size: 16px;
}


.random-character .status {
    align-items: baseline;
}

.random-character .status div {
    width: 12px;
    height: 12px;
    margin-right: 5px;
    border-radius: 50%;
}

.random-character .green {
    background-color: chartreuse;
}

.random-character .red {
    background-color: red;
}

.random-character .orange {
    background-color: orange;
}
